import { UpdateActionParams } from './types';
import { Edit } from '@wix/wix-ui-icons-common';
import React from 'react';

export const updateAction = (params: UpdateActionParams) => {
  const { action, sdk, actionParams } = params;
  const { appConfig } = sdk;

  return {
    onClick: () => {
      if (action.update.mode === 'page') {
        const pageId = action.update.page.id;
        const pageToNavigate = appConfig.pages.find(
          (page) => page.id === pageId,
        );

        if (pageToNavigate) {
          if (pageToNavigate.type === 'entityPage') {
            const { navigateToEntityPage } = sdk;
            const item = actionParams.item;
            if (navigateToEntityPage && item) {
              navigateToEntityPage({
                item,
                entityPageId: pageId,
              });
            }
          }
        }
      }
    },
    label: 'Update',
    icon: <Edit />,
  };
};
